.App {
  font-family: sans-serif;
  text-align: center;
}

.todo__list {
  background-color: #3a25ee;
  display: inline-flex;
  padding: 20px;
  color: #ffffff;
  font-size: 20px;
  font-weight: 500;
  border-radius: 50px;
  align-items: center;
}

.danger {
  background-color: red;
  border: none;
  color: #ffffff;
  margin-left: 25px;
  border-radius: 5px;
  font-size: 16px;
  padding: 7px;
}
